.custom-button {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  margin: auto;
  text-align: center;
  padding: 0 30px 0 30px;
  font-size: 14px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: white;
    color: black;
  }

  &.google-sign-in {
    background-color: #4285f4;
    color: white;

  &:hover {
    background-color: #357ae8;
  }
  }
  
}
